<script>
// eslint-disable-next-line object-curly-newline
import axios from 'axios'
import { computed, onMounted, ref, reactive, defineComponent } from '@vue/composition-api'
import { mdiPoll, mdiLabelVariantOutline, mdiCurrencyUsd, mdiHelpCircleOutline } from '@mdi/js'
import StatisticsCardVertical from '@/components/statistics-card/StatisticsCardVertical.vue'

// demos
import DashboardCongratulationJohn from './DashboardCongratulationJohn.vue'
import DashboardStatisticsCard from './DashboardStatisticsCard.vue'
import DashboardCardTotalEarning from './DashboardCardTotalEarning.vue'
import DashboardCardDepositAndWithdraw from './DashboardCardDepositAndWithdraw.vue'
import DashboardCardSalesByCountries from './DashboardCardSalesByCountries.vue'
import DashboardWeeklyOverview from './DashboardWeeklyOverview.vue'
import DashboardDatatable from './DashboardDatatable.vue'

export default {
  components: {
    StatisticsCardVertical,
    DashboardCongratulationJohn,
    DashboardStatisticsCard,
    DashboardCardTotalEarning,
    DashboardCardDepositAndWithdraw,
    DashboardCardSalesByCountries,
    DashboardWeeklyOverview,
    DashboardDatatable,
  },
  setup() {
    onMounted(() => {
      fetchServices()
      fetchUsers()
    })
    const totalProfit = {
      statTitle: 'Total Profit',
      icon: mdiPoll,
      color: 'success',
      subtitle: 'Weekly Project',
      statistics: '$25.6k',
      change: '+42%',
    }

    const totalSales = {
      statTitle: 'Refunds',
      icon: mdiCurrencyUsd,
      color: 'secondary',
      subtitle: 'Past Month',
      statistics: '$78',
      change: '-15%',
    }

    // vertical card options
    const newProject = {
      statTitle: 'New Project',
      icon: mdiLabelVariantOutline,
      color: 'primary',
      subtitle: 'Yearly Project',
      statistics: '862',
      change: '-18%',
    }

    const salesQueries = {
      statTitle: 'Sales Quries',
      icon: mdiHelpCircleOutline,
      color: 'warning',
      subtitle: 'Last week',
      statistics: '15',
      change: '-18%',
    }
    let sendData = ref([])
    let upData = ref([])
    let tab = ref(1)
    let remarks = ref('')
    let jobId = ref('')
    let ServiceList = ref([])
    let users_ = reactive([])
    let assignedTo = ref([])
    let jobassigndialog = ref(false)
    function fetchServices()
    {
          
            let apiURL = `api/postAPI/loadServicesunassigned`;
            axios.get(apiURL).then(res => {
             
              ServiceList.value = res.data;    
              }).catch(error => {
                console.log(error)
             });
    }
    function fetchUsers()
    {
          
            let apiURL = `api/postApi/loadusers`;
            axios.get(apiURL).then(res => {
              users_.push('Public')
              for(let i = 0;i<res.data.length;i++)
                {
                  // console.log(res.data[i].Name)
                  users_.push(res.data[i].Name)
                }
              }).catch(error => {
                console.log(error)
             });
    }
    function changeRoute(a)
    {
      console.log(a)
    }
    function openjobassigndialog(id)
    {
     jobId.value = id
     jobassigndialog.value = true
    }
    function savejobassign()
    {
       let obj = 
        {
          jobId:jobId.value,
          assignedTo:assignedTo.value,
          assignedby:localStorage.getItem("loggedUser"),
          remarks:remarks.value,
          date:new Date().toLocaleDateString("en-US")
          
        }
       
          let apiURL = 'api/statusChangeAPI/create-Jobassign';
          axios.post(apiURL, obj).then(res => {
          if(res.data != null)
          {
           
            // fetchServices()
            // console.log(res.data)
            updatejobStatusinService(jobId.value)
           
          }else
          {
            console.log(res.data)
          }
          
          
        }).catch(error => {
          console.log(error)
        });
      
    }
    function updatejobStatusinService(Id)
    {
      console.log("IDD"+Id)
      let obj = {
        assignedStatus:1,
        assignedTo:assignedTo.value
      }
       //  console.log(this.S_)
       let apiURL = `api/postAPI/update-service/${Id}`;
          axios.post(apiURL, obj).then((res) => {
          // console.log(res)
          if(res.status == 200)
          {
               fetchServices()
               console.log("success"+res)
               jobassigndialog.value = false
          }else
          {
              console.log("error"+res)
          }
          // this.close();
          // this.loadCustomer();
          
        }).catch(error => {
          console.log(error)
          console.log(this.$route.params.id)
        })
    }

    return {
      updatejobStatusinService,
      jobId,
      sendData,
      totalProfit,
      totalSales,
      newProject,
      salesQueries,
      remarks,
      tab,
      fetchServices,
      ServiceList,
      users_,
      fetchUsers,
      assignedTo,
      changeRoute,
      jobassigndialog,
      openjobassigndialog,
      savejobassign,
      upData,
    }
  },
}
</script>
